* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  margin:0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#root > img {
  margin-top: 80px;
  width: 60%;
  height: auto;
  opacity: 1;
}

body{
  font-family: 'Roboto Mono', monospace;
  background: #F3F3F3;
  width: 100%;
}
nav{
  position: fixed;
  z-index: 3;
  top: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #F3F3F3;
}

.Nav--content{
  display: flex;
  color: black;
  height: minmax(10vh, 70px);
  width: 100%;
  align-items: center;
}

/* .Nav--content > h2{
  width: fit-content;
  margin: 15px;
  font-size: 22px;
} */

.Nav--content > img {
  width: 42px;
  height:  42px;
  margin: 5px;
  margin-right: auto;
}

hr {
  width: 80%;
  border: 1px solid #FD6883;
  margin: 0;;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
}

header {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 70px;
  margin-top: 0;
}
.header-card {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

header  img{
  width: 30vw;
  height: 30vw;
  margin-right: 35px;
  border-radius: 20vw;
  z-index: 2;
  /* box-shadow: 10px 15px 10px rgba(0, 0, 0, 0.25); */
}

.header--container {
  width: 44vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.intro {
  color: black;
  /* font-family: 'Roboto Mono', monospace; */
  font-size: 3vw;
  margin: 0 0 10px 0;
  width: fit-content;
}

.school{
  color: #FD6883;
  font-size: 1.5vw;
  margin: 0 0 30px 0;
  opacity: 0;
}

.about{
  font-size: 1.2vw;
  color: black;
  opacity:  0;
}

.typed {
  overflow: hidden;
  border-right: 4px solid #FD6883;
  white-space: nowrap;
  animation: type 3s steps(15,end), blink 1s infinite;
  /* animation-delay: 2s; */
}

@keyframes type {
  0% { width: 0;}
  33% { width: 0;}
  100% { width: 27.5vw; }
}

@keyframes blink {
  from { border-color: transparent; }
  to { border-color: #FD6883; }
}

@media screen and (max-width:1100px){
  .Nav--content h2 {
    font-size: 2vw;
  }
}